  background: #ffffff;
  border: 5px solid #e5e5e5;
  outline: none;
}
input[type="text"] {
  margin-left: 15px;
  margin-top: 5px;
}
input[type="tel"],
input[type="email"] {
  width: 100%;
  margin-top: 7px;
}
input[type="tel"] + span::after {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  right: 0;
  width: 56px;
  height: 56px;
  background: #e6e6e6 url("../img/icon-phone.svg") no-repeat center;
}
input[type="email"] + span::after {
  content: "";
  display: block;
  position: absolute;
  top: 30px;
  right: 0;
  width: 56px;
  height: 56px;
  background: #e6e6e6 url("../img/icon-mail.svg") no-repeat center;
}
input::placeholder,
textarea::placeholder {
  font-family: inherit;
  font-size: 16px;
  font-weight: bold;
  color: #000000;
}
input[type="radio"] + label {
  display: block;
  position: relative;
  margin-top: 40px;
  margin-bottom: 37px;
  padding-left: 45px;
  font-size: 17px;
  text-transform: none;
  font-weight: bold;
  cursor: pointer;
}
input[type="radio"] + label::before {
  content: "";
  display: block;
  position: absolute;
  top: -13px;
  left: 0;
  width: 56px;
  height: 56px;
  background-color: #f2f2f2;
  border-radius: 50%;
}
input[type="radio"]:checked + label::before {
  width: 20px;
  height: 20px;
  background-color: #000000;
  border: 18px solid #f2f2f2;
}
input[type="checkbox"] + label {
  display: block;
  position: relative;
  margin-top: 40px;
  margin-bottom: 45px;
  padding-left: 45px;
  font-size: 17px;
  text-transform: none;
  font-weight: bold;
  cursor: pointer;
}
input[type="checkbox"] + label::before {
  content: "";
  display: block;
  position: absolute;
  top: -13px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border: 5px solid transparent;
  border-radius: 10%;
}
input[type="checkbox"]:checked + label::before {
  background: #f2f2f2 url("../img/icon-tick.svg") no-repeat center;
}
input[type="checkbox"]:focus + label::before {
  border: 5px solid #e5e5e5;
}
