@font-face {
  font-weight: normal;
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: fallback;
}
@font-face {
  font-weight: bold;
  font-family: "PT Sans Bold";
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: fallback;
}
body {
  width: 320px;
  margin: 0 auto;
  font-family: "PT Sans", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.6;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title {
  font-family: "PT Sans Bold";
  font-size: 18px;
  line-height: 1.3;
}
picture {
  line-height: 0;
}
@media (min-width: 768px) {
  body {
    width: 768px;
  }
  .title {
    font-size: 22px;
  }
}
@media (min-width: 1200px) {
  body {
    width: 1200px;
  }
}
